export interface APIOptions {

  pointerSelection?: Boolean

  fill?: string 

  fillOpacity?: number 

  stroke?: string

  strokeOpacity?: number

}

export const initDefaults = (opts: APIOptions): APIOptions => {

  const dopts = { ...opts };

  // pointerSelection: default = true
  if (dopts.pointerSelection === undefined)
    dopts.pointerSelection = true;

  return dopts;

}

